.projects {
    background-color: #f5f5f5;
    padding: 40px 0;
}

.projectsContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.projectsBlock {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}